.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(auto, 260px));
  grid-gap: 0.5%;
  padding: 3%;
  width: 80%;
  padding-top: 70px;
}

@media screen and (max-width: 600px) {
  .movies {
    grid-template-columns: repeat(2, minmax(auto, 150px));
    max-width: 100%;
  }
}
